// ==========================================================================
// BUTTONS
// ==========================================================================

.mainBttn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 1em 0 2em;
  padding: 1em 2.5em;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-family: Arial;
  font-weight: 500;
  letter-spacing: normal;
  font-size: 16px;
  line-height: 1.8;
  white-space: nowrap;

  .mainBttn__border, .mainBttn__bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    // box-sizing: border-box;
    pointer-events: none;
  }

  .mainBttn__border {
    border: 1px solid #000;
    opacity: 0.18;
    transition: transform 0.4s cubic-bezier(0.19, 1, 0.22, 1), opacity 0.4s cubic-bezier(0.19, 1, 0.22, 1);
    will-change: transform;
  }

  .mainBttn__bg {
    background: #000;
    opacity: 0;
    transform: scale(0.6);
    transition: transform .7s cubic-bezier(0.19, 1, 0.22, 1), opacity .15s cubic-bezier(0.165, 0.84, 0.44, 1);
    will-change: opacity, transform;
  }

  .mainBttn__text {
    color: #000;
    transition: color 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
    will-change: color;
    z-index: 1;
  }

  &:hover {
    text-decoration: none;

    .mainBttn__border {
      opacity: 0;
      transform: scale(1.2, 1.44);
      transition: transform .4s cubic-bezier(0.19, 1, 0.22, 1), opacity 0.3s cubic-bezier(0.19, 1, 0.22, 1);
    }

    .mainBttn__bg {
      opacity: 1;
      transform: scale(1);
      transition: transform 0.9s cubic-bezier(0.19, 1, 0.22, 1), opacity .6s cubic-bezier(0.165, 0.84, 0.44, 1);
    }

    .mainBttn__text {
      color: #fff;
      transition: fill 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
    }

  }
}
