/**
 * Basic styles for links
 */
a {
  // color: $brand-color;
  text-decoration: none;

  @include on-event {
    // color: $text-color;
    text-decoration: underline;
  }
}



img {
  display: none;
}




a:focus {
    outline: 0
}

a:link {
    -webkit-tap-highlight-color: rgba(255,255,255,0);
}

.a,a {
    color: #ff585d;
    font-weight: 400;
    text-decoration: none;
    cursor: pointer
}

.a:focus,.a:hover,a:focus,a:hover {
    color: #ff797d;
    text-decoration: underline
}


.a--underlines a {
    color: #ff585d;
    font-weight: 400;
    text-decoration: none;
    cursor: pointer;

    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-perspective: 1000;
    perspective: 1000;
    display: inline-block;
    position: relative
}

.a--underlines a::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    -webkit-transform: scaleX(0);
    -ms-transform: scaleX(0);
    transform: scaleX(0);
    height: 0.125rem;
    bottom: 0.125rem;
    left: 0.0625rem;
    background-color: #ff585d;
    text-decoration: none;
    -webkit-transform-origin: 100% 50%;
    -ms-transform-origin: 100% 50%;
    transform-origin: 100% 50%;
    transition: -webkit-transform .4s cubic-bezier(.23,1,.32,1);
    transition: transform .4s cubic-bezier(.23,1,.32,1);
    transition: transform .4s cubic-bezier(.23,1,.32,1), -webkit-transform .4s cubic-bezier(.23,1,.32,1);
    will-change: transform
}

.a--underlines a:focus,.a--underlines a:hover {
    text-decoration: none
}

.a--underlines a:focus::after,.a--underlines a:hover::after {
    -webkit-transform: scaleX(1);
    -ms-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transform-origin: 0 50%;
    -ms-transform-origin: 0 50%;
    transform-origin: 0 50%
}

.a--underlines a:focus,.a--underlines a:hover {
    color: #ff585d
}
